


.container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 2rem;
  background-color: #fff;
  box-shadow: 0 0 10px #d3d3d3;
  border-radius: 5px;
  padding: 10px 15px;
}
